.project__item {
    width: 100%;
    max-width: var(--project-item-max-width);

    color: white;
    font-family: var(--font-family-main);

    & > img {
        max-width: 4rem;
        margin: 0 auto 0.5rem auto;
    }

    &__container {
        display: none;
        visibility: hidden;
    
        &__banner {
            display: flex;
            justify-content: center;
            align-items: center;
            background-image: var(--project-item-background);
            padding: .5rem 0;
    
            & > h2 {
                font-weight: var(--font-weight-h2);
                font-size: var(--font-size-h2);
            }
        }
    
        & > h3 {
            margin-top: 1rem;
            text-align: center;
            font-weight: var(--font-weight-h3);
            font-size: var(--font-size-h3);
        }
    
        & > ul {
            font-weight: var(--font-weight-ul);
            font-size: var(--font-size-ul);
    
            & > li {
                margin-top: 0.5rem;
            }
        }
    
        & > ul > ::marker {
            content: '\\ ';
            font-size: 2.0ch;
        }
    }
}

@media screen and (min-width: 500px) {
    .project__item {
        margin: 0 auto;

        & > img {
            cursor: pointer;
            max-width: 7.5rem;          
            
            margin-bottom: 1rem;

            animation-name: rotateInfini;
            animation-iteration-count: infinite;
            animation-duration: 5s;
            animation-play-state: paused;

            &:hover {
                animation-play-state: running;
            }
        }

        &__container {

            & > ul {
                font-size: var(--font-size-h2);
            }

            &__banner {
                background-image: var(--project-item-background-gt-500);

                & > h2 {
                    font-size: var(--font-size-h1);
                }
            }
        }
    }
}

@keyframes slideOut {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes rotateInfini {
    from {
        transform: rotate(0deg);
    }

    50% {
        transform: rotate(180deg);
    }
    
    to {
        transform: rotate(0deg);
    }
}