:root {
  /* FONT FAMILIES */
  --font-family-main: 'Montserrat', sans-serif;

  /* COMMON FONT PROPERTIES */
  --font-size-h1: 1.5rem;
  --font-size-h1-S: 1.05rem;
  --font-size-h2: 1rem;
  --font-size-h3: 1rem;
  --font-size-h4: .6rem;
  --font-size-ul: 0.8rem;
  --font-size-p : 0.8rem;
  --font-size-a : 0.75rem;

  --font-weight-h1: 700;
  --font-weight-h2: 600;
  --font-weight-h3: 500;
  --font-weight-h4: 600;
  --font-weight-ul: 600;
  --font-weight-p : 600;
  --font-weight-a : 400;

  --font-accent-color: rgb(243, 54, 54);

  /* IMAGE PROPERTIES */
  --logo-max-width: 10rem;

  /* BANNER PROPERTIES (SPAN) */
  --banner-width: 80%;
  --banner-height: .5rem;
  --banner-background: linear-gradient(45deg, black, red, black);

  /* SOCIAL MEDIA BANNER */
  --socialmedia-max-width: 400px;
  --socialmedia-item-flex: 1 1 50px;
  --socialmedia-item-image-max-width: 2.5rem;

  /* PROJECT PROPERTIES */
  --project-item-max-width: 500px;

  --project-item-background: linear-gradient(45deg, rgb(252, 55, 55), red, black);
  --project-item-background-gt-500: linear-gradient(90deg, black, rgb(252, 55, 55), red, black);

  --project-leftbar-flex: 1 1 30%;
  --project-rightbar-flex: 1 1 70%;

  --project-plus-icon-width: 30px;
  --project-counter-font-size: 1.5rem;

  /* KNOWHOW + SKILLCARD PROPERTIES */
  --knowhow-accent-color: rgb(221, 41, 41);

  --skillcard-background-color: rgb(44, 44, 44);

  /* IMAGE GALLERY PROPERTIES */
  --gallery-border-color: rgb(255, 255, 255);

  /* BLOG PROPERTIES */
  --blog-item-max-width: 300px;

  --blog-item-background: rgb(39, 39, 39);

  --blog-item-border-radius: .5rem .5rem 0 0;
  --blog-item-text-shadow: 1px 1px 3px white;
  --blog-item-span-height: 5px;
  --blog-item-span-background: linear-gradient(45deg, rgb(39, 39, 39), red, rgb(39, 39, 39));
  --blog-item-filter-drop-shadow: drop-shadow(0px 0px 1rem rgb(226, 33, 33));
}

body::-webkit-scrollbar {
  width: auto;         
}

body::-webkit-scrollbar-track {
  background: inherit;      
}

body::-webkit-scrollbar-thumb {
  background-color: rgb(247, 31, 31);   
  border-radius: 3rem;
}

body::-webkit-scrollbar-thumb:hover {
  background-color: rgb(209, 28, 4);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(192, 12, 12);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role='list'],
ol[role='list'] {
  list-style: none;
}

html {
  scroll-behavior: smooth;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  background: black;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
   scroll-behavior: auto;
  }
  
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}