.homepage-jobs {
    
    & > h1 {
        margin-bottom: 1rem;

        font-family: var(--font-family-main);
        font-weight: var(--font-weight-h1);
        font-size: var(--font-size-h1);
        color: white;
        text-align: center;
    }

    &__container {
        display: grid;
        grid-template-columns: 1fr;

        &-item {

            & > *:not(:first-child) {
                display: none;
            }

            & > img {
                max-width: 5rem;
                width: 100%;
                margin: 0 auto;
                filter: grayscale(1);
            }

            & > h2 {
                font-family: var(--font-family-main);
                font-weight: var(--font-weight-h2);
                font-size: var(--font-size-h2);
                color: white;
                text-align: center;
            }

            & > p {
                font-family: var(--font-family-main);
                font-weight: var(--font-weight-p);
                font-size: var(--font-size-p);
                color: white;
                margin: .5rem 2rem;
            }
        }
    }
}

@media only screen and (min-width: 450px) {
    .homepage-jobs {
        &__container {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(225px, 1fr));
        }
    }
}

@media only screen and (min-width: 800px) {
    .homepage-jobs {
        &__container {
            display: grid;
            grid-template-columns: 1fr repeat(3, calc(800px/3)) 1fr;
            grid-template-rows: repeat(2, 35vh);

            &-item {
                & > h2 {
                    font-size: calc(var(--font-size-h2) + .25rem);
                }

                & > p {
                    margin: .5rem .5rem 0 .5rem;
                    font-size: calc(var(--font-size-p) + .15rem);
                }

                & > img {
                    max-width: 7rem;
                    cursor: pointer;
                    filter: grayscale(1);
                    transition: filter .5s ease-in-out;

                    &:hover {
                        filter: grayscale(0);
                    }
                }

                & > *:not(:first-child) {
                    display: none;
                }
            }

            &-item:nth-child(1) {
                grid-column: 2 / 3;
                grid-row: 1 / 2;
            }

            &-item:nth-child(2) {
                grid-column: 4 / 5;
                grid-row: 1 / 2;
            }  

            &-item:nth-child(3) {
                grid-column: 3 / 4;
                grid-row: 2 / 3;
            }
        }
    }
}