.homepage-knowhow {
    margin-bottom: 2rem;

    & > h1 {
        font-family: var(--font-family-main);
        font-weight: var(--font-weight-h1);
        font-size: var(--font-size-h1);
        color: white;
        text-align: center;
    }

    & > h2 {
        margin-top: 1rem;
        font-family: var(--font-family-main);
        font-weight: var(--font-weight-h2);
        font-size: var(--font-size-h2);
        color: white;
        text-align: center;
    }

    &__toolheading {
        margin-top: 5rem !important;
    }

    &__container {
        margin: 2rem auto 0 auto;

        display: grid;
        grid-template-columns: 1fr 165px 1fr;
        gap: 1.5rem;
        justify-content: center;

        & > button {
            align-self: center;
            min-width: 2.5rem;
            max-width: 2.5rem;
            height: max-content;
            cursor: pointer;
            border: none;
            background: none;

            &:first-child {
                width: 100%;
                justify-self: flex-end;
            }
        }
    }
}

@media screen and (min-width: 600px) {
    .homepage-knowhow {

        &__container {
            margin: 4rem auto 2rem auto;
            grid-template-columns: 1fr 165px 165px 165px 1fr;
            gap: 0.5rem;
        }
    }

    .knowhow--accent {
        transform: scale(1.2);
        background: var(--knowhow-accent-color)!important;
    }
}

@media screen and (min-width: 1400px) {
    .homepage-knowhow {
        display: grid;
        grid-template-columns: 1fr 600px 600px 1fr;
        grid-template-rows: repeat(3, min-content);
        grid-template-areas: 
            ". title title ."
            ". subhead1 subhead2 ."
            ". skillset1 skillset2 .";
        gap: 0rem 4rem;

        &__container {

            & > button {
                width: 4rem;
            }
        }

        & > h1 {
            grid-area: title;
        }

        &__plheading {
            grid-area: subhead1;
            align-self: center;
        }

        &__container--1 {
            grid-area: skillset1;
        }

        &__toolheading {
            grid-area: subhead2;
            align-self: center;
            margin-top: 0 !important;
        }

        &__container--2 {
            grid-area: skillset2;
        }
    }
}