.footer {
    padding-bottom: 5rem;
    display: flex;
    flex-direction: column;

    &__up {
        align-self: center;

        & > a {

            & > img {
                min-width: 2.5rem;
            }
        }
    }

    & > h2 {
        margin: 2rem 1rem;

        font-family: var(--font-family-main);
        font-weight: var(--font-weight-h2);
        font-size: var(--font-size-h2);
        color: white;
        text-align: center;
        text-transform: uppercase;

        animation: glowing-text 5s ease-in-out infinite;
        --glow-color: white;
    }

    & > span {
        display: block;
        width: var(--banner-width);
        height: var(--banner-height);
        margin: 0 auto;
        background: var(--banner-background);
    }

    &__banner {

        &--1 {
            margin-bottom: 1rem !important;
        }

        &--2 {
            margin-top: 1rem !important;
        }
    }

    &__details {    
        margin: 0 1rem;
        align-self: center;
        display: flex;

        & > img {
            width: 100%;
            max-width: 4rem;
        }

        & > h4 {
            align-self: center;
            font-family: var(--font-family-main);
            font-weight: var(--font-weight-h4);
            font-size: var(--font-size-h4);
            color: white;
        }
    }

    &__impressum {
        display: flex;
        flex-direction: column;
        align-self: center;
        margin: 1rem 0 0 0;

        & > h2 {
            font-family: var(--font-family-main);
            font-weight: var(--font-weight-h2);
            font-size: var(--font-size-h2);
            color: white;
        }

        & > ul {
            color: white;
            list-style: none;
        }
    }
}

@keyframes glowing-text {
    from {
        text-shadow: 1px 1px 0px var(--glow-color);
    }

    50% {
        text-shadow: 1px 1px 10rem var(--glow-color);
    }

    to {
        text-shadow: 1px 1px 0px var(--glow-color);
    }
}