#homepage {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-areas: 
        "header"
        "projects"
        "jobs"
        "aboutme"
        "education"
        "publication"
        "knowhow"
        "gallery"
        "blog"
        "footer";
}

#homepage-header {
    grid-area: header;
}

#homepage-projects {
    grid-area: projects;
    margin-top: 2rem;
}

#homepage-jobs {
    grid-area: jobs;
    margin: 2rem 0 4rem 0;
}

#homepage-aboutme {
    grid-area: aboutme;
}

#homepage-education {
    grid-area: education;
}

#homepage-publication {
    grid-area: publication;
}

#homepage-knowhow {
    grid-area: knowhow;
}

#homepage-gallery {
    grid-area: gallery;
}

#homepage-blog {
    grid-area: blog;
}

#footer {
    grid-area: footer;
    margin-top: 2rem;
}