.homepage-gallery {
    margin-top: 2rem;

    & > h1 {
        font-family: var(--font-family-main);
        font-weight: var(--font-weight-h1);
        font-size: var(--font-size-h1);
        color: white;
        text-align: center;
    }

    &__imagecontainer {
        margin: 2rem 0;
        display: grid;
        grid-template-columns: 1fr;
        gap: 1rem;

        & > picture {
            width: 100%;
            border: .5rem solid var(--gallery-border-color);

            & > img {
                width: 100%;
                height: 100%;
            }
        }
    }
}

@media screen and (min-width: 500px) {
    .homepage-gallery {

        &__imagecontainer {

            grid-template-columns: 230px 230px;
            justify-content: center;
        }
    }
}

@media screen and (min-width: 1000px) {
    .homepage-gallery {

        &__imagecontainer {
            grid-template-columns: repeat(auto-fit, 250px);
            justify-content: center;
        }
    }
}

@media screen and (min-width: 1300px) {
    .homepage-gallery {

        &__imagecontainer {
            margin: 2rem auto;
            max-width: 1250px;
            grid-template-columns: repeat(auto-fit, 250px);
            justify-content: center;
        }
    }
}