.homepage-projects {
    & > h1 {
        margin-bottom: 3.5rem;

        font-family: var(--font-family-main);
        font-weight: var(--font-weight-h1);
        font-size: var(--font-size-h1);
        color: white;
        text-align: center;
    }
}

@media screen and (min-width: 500px) {
    .homepage-projects {
        &__items {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(var(--project-item-max-width), 1fr));
        }
    }
}