.skillcard {
    max-width: 10rem;
    min-height: 18rem;
    background: var(--skillcard-background-color);
    padding: 3rem 1rem;
    border-radius: 5px;

    & > img {
        width: 100%;
        max-width: 4rem;
        margin: 0 auto;
    }

    & > h2 {
        font-family: var(--font-family-main);
        font-weight: var(--font-weight-h2);
        font-size: var(--font-size-h2);
        color: white;
        text-align: center;
        text-decoration: overline underline;
    }

    & > h3 {
        font-family: var(--font-family-main);
        font-weight: var(--font-weight-h3);
        font-size: var(--font-size-h3);
        color: white;
        text-align: center;
    }

    & > p {
        font-family: var(--font-family-main);
        font-weight: var(--font-weight-p);
        font-size: var(--font-size-p);
        color: white;
        text-align: center;
    }
}

.active {
    display: block;
}

.inactive {
    display: none;
}