#homepage-header {
    & > img {
        width: 100%;
        max-width: var(--logo-max-width);

        margin: 2rem auto 0rem auto;
    }

    & > h1 {
        font-family: var(--font-family-main);
        font-weight: var(--font-weight-h1);
        font-size: var(--font-size-h1-S);
        color: white;

        text-align: center;

        & > span:nth-child(1):before {
            content: '';
            animation: content-rotator infinite 15s;
        }

        & > span:nth-child(1) {
            color: var(--font-accent-color);
        }

        & > span:last-child {
            color: var(--font-accent-color);
        }
    }

    & > span {
        display: block;
        width: var(--banner-width);
        height: var(--banner-height);
        background: var(--banner-background);
        margin: 3rem auto;
    }

    & > button {
        display: none;
    }
}

.homepage-header__socialmedia {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 1rem;

    max-width: var(--socialmedia-max-width);
    margin: 0 auto 2rem auto;

    &-item {
        flex: var(--socialmedia-item-flex);

        & > a {
            display: block;
            max-width: var(--socialmedia-item-image-max-width);
            margin: 0 auto;
            cursor: pointer;

            & > img {
                max-width: var(--socialmedia-item-image-max-width);
                width: 100%;
                margin: 0 auto;

                filter: grayscale(1);
                transition: filter .5s ease-in-out;

                &:hover {
                    filter: grayscale(0);
                }
            }
        }
    }
}

@media only screen and (min-width: 700px) {
    #homepage-header {
        height: 100vh;
        margin-top: 10vh;

        & > h1 {
            max-width: 500px;
            margin: 0 auto;

            font-family: var(--font-family-main);
            font-weight: var(--font-weight-h1);
            font-size: var(--font-size-h1);
            color: white;
            text-align: center;
        }

        & > button {
            display: block;
            transform: rotate(180deg);
            
            cursor: pointer;
            border: none;
            background-color: transparent;
            
            margin: 5rem auto 0 auto;

            & > img {
                min-width: 3.5rem;
            }
        }
    }
}

@keyframes content-rotator {
    from {
        content: 'M.Sc. Physics';
    }

    20% {
        content: 'Software Developer';
    }

    40% {
        content: 'Photographer';
    }

    60% {
        content: 'Poetry Writer';
    }

    80% {
        content: 'Martial Artist';
    }

    to {
        content: 'M.Sc. Physics';
    }
}