.homepage-aboutme {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;

    & > h1 {
        font-family: var(--font-family-main);
        font-weight: var(--font-weight-h1);
        font-size: var(--font-size-h1);
        color: white;
        text-align: center;
    }

    & > img {
        max-width: 20rem;
        width: 100%;
        margin: 0 auto;
    }

    & > p {
        max-width: 500px;
        justify-self: center;

        font-family: var(--font-family-main);
        font-weight: var(--font-weight-p);
        font-size: var(--font-size-p);
        color: white;
        margin: 0 2rem 2rem 2rem;
    }
}


@media screen and (min-width: 800px) {
    .homepage-aboutme {
        grid-template-columns: 1fr 400px 400px 1fr;
        grid-template-areas: 
            ". title title ."
            ". image desc .";

        & > h1 {
            grid-area: title;
        }

        & > img {
            grid-area: image;
        }

        & > p {
            font-size: calc(var(--font-size-p) + .25vw);
            grid-area: desc;
            align-self: center;
        }
    }
}